html {
  position: relative;
  min-height: 100%;
}

body {
  overflow-x: hidden;
}

/*rtl:options:
{
    "aliases": {
        "left-side-menu": "left-side-menu"
    }
}*/
#wrapper {
  height: 100%;
  overflow: hidden;
  width: 100%;
}

.content-page {
  margin-left: 240px;
  overflow: hidden;
  padding: 0 12px 65px 12px;
  min-height: 80vh;
  margin-top: 70px;
}

.left-side-menu {
  width: 240px;
  background: var(--ct-bg-leftbar);
  bottom: 0;
  padding: 20px 0;
  position: fixed;
  transition: all 0.1s ease-out;
  top: 70px;
  box-shadow: var(--ct-box-shadow);
}
.left-side-menu .user-box {
  display: none;
}
.left-side-menu .user-box .img-thumbnail {
  background-color: inherit;
  border-color: var(--ct-left-menu-user-box-border);
}

.user-box .user-name {
  color: #6c757d;
}

#sidebar-menu > ul {
  list-style: none;
  padding: 0;
}
#sidebar-menu > ul > li > a {
  color: var(--ct-menu-item);
  display: block;
  padding: 12px 20px;
  position: relative;
  transition: all 0.4s;
  font-family: "Karla", sans-serif;
  font-size: 0.95rem;
}
#sidebar-menu > ul > li > a:hover, #sidebar-menu > ul > li > a:focus, #sidebar-menu > ul > li > a:active {
  color: var(--ct-menu-item-hover);
